import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import * as api from './../api/api';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';

import HelpDialog from './HelpDialog';

import { view } from '@risingstack/react-easy-state';
import { user } from './../store';

const useStyles = makeStyles((theme) => ({
	bg: {
		background: theme.palette.background.paper,
	},
	root: {
		display: 'flex',
		flexWrap: 'wrap',
		justifyContent: 'flex-start',
	},
	item: {
		margin: 0,
		marginRight: 2,
		marginBottom: 2,
	},
	progressBox: {
		width: '100%',
		display: 'flex',
		alignItems: 'center',
	},
	progress: {
		flexGrow: '1',
	},
}));

const ActionButtons = (props) => {
	const classes = useStyles();
	const [desc, setDesc] = useState('');
	const [albumPlace, setAlbumPlace] = useState('');
	const [helpOpen, setHelpOpen] = useState(false);

	function descChange(e) {
		setDesc(e.target.value);
	}

	const handleDialogClose = () => {
		setHelpOpen(false);
		console.log(helpOpen);
	};

	return (
		<div>
			<div className={classes.root}>
				<div className={classes.progressBox}>
					{/* <Typography variant="body1">Current Task</Typography> */}
					<LinearProgress
						variant="determinate"
						value={user.progress.percent}
						color="secondary"
						className={classes.progress}
						style={{ display: 'none' }}
					/>
				</div>
				<Typography variant="body1" style={{ flexBasis: '100%', paddingTop: 5 }}>
					Add Albums at the
				</Typography>
				<div>
					<Button
						variant="contained"
						color="primary"
						className={classes.item}
						onClick={() => {
							api.addSelectedAlbumsToSelectedPlaylist('start');
						}}
						size="small"
					>
						start
					</Button>
					<Button
						variant="contained"
						color="primary"
						className={classes.item}
						onClick={() => {
							api.addSelectedAlbumsToSelectedPlaylist('end');
						}}
						size="small"
					>
						end
					</Button>
				</div>
				<div>
					<input
						id="nth-place"
						placeholder="n"
						style={{ width: '50px', padding: 5, marginRight: 5, borderRadius: 4 }}
						onChange={(e) => setAlbumPlace(e.target.value)}
						type="text"
					/>
					<Button
						variant="contained"
						color="primary"
						className={classes.item}
						onClick={() => {
							api.addSelectedAlbumsToSelectedPlaylist('nth');
						}}
						size="small"
					>
						{albumPlace === '' ? 'n' : albumPlace}th place
					</Button>
				</div>

				<Button
					variant="contained"
					color="primary"
					className={classes.item}
					onClick={() => {
						let yeahOkay = window.confirm(
							'You are about to replace the contents of these playlists with these albums. Is that what you want to do?'
						);
						if (yeahOkay) {
							api.addSelectedAlbumsToSelectedPlaylist('start', 'replace');
						}
					}}
					size="small"
				>
					Replace all
				</Button>
				<Typography variant="body1" style={{ flexBasis: '100%', paddingTop: 5 }}>
					Playlist Actions
				</Typography>
				<div style={{ textAlign: 'left' }}>
					<input
						id="new-description"
						placeholder="Description"
						onChange={descChange}
						style={{ width: '120px', padding: 5, marginRight: 5, borderRadius: 4 }}
						type="text"
					/>
					<Button
						variant="contained"
						color="primary"
						className={classes.item}
						onClick={() => {
							api.replaceDescription(desc);
						}}
						size="small"
					>
						Replace
					</Button>
					<Button
						variant="contained"
						color="primary"
						className={classes.item}
						onClick={() => {
							api.refreshPlaylists();
						}}
						size="small"
					>
						refresh albums
					</Button>
				</div>
				<Typography variant="body1" style={{ flexBasis: '100%', paddingTop: 5 }}>
					Utility
				</Typography>
				<Button
					variant="contained"
					color="primary"
					className={classes.item}
					onClick={() => {
						alert('Spotify account deleted. Please refresh page.');
					}}
					size="small"
				>
					Delete Spotify
				</Button>
				<Button
					variant="contained"
					color="primary"
					className={classes.item}
					onClick={() => {
						setHelpOpen(true);
					}}
					size="small"
				>
					I'm getting an error!
				</Button>
				<HelpDialog open={helpOpen} onClose={handleDialogClose} />
			</div>
		</div>
	);
};

export default view(ActionButtons);
